body {
  overflow-x: hidden;
}
#preloader {
  z-index: 9999;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-transition: opacity 0.3s linear, visibility 0.2s linear 0.3s;
  transition: opacity 0.3s linear, visibility 0.2s linear 0.3s;
  opacity: 1;
}
#preloader.close {
  opacity: 0;
  visibility: hidden;
}
.preloader {
  width: 200px;
  height: 200px;
  border: 3px solid transparent;
  position: relative;
  border-radius: 50%;
  border-top-color: #3acfd5;
  -webkit-animation: r1 0.6s cubic-bezier(0.44, 0.39, 0.32, 1.28) infinite;
  animation: r1 0.6s cubic-bezier(0.44, 0.39, 0.32, 1.28) infinite;
}
.preloader::after,
.preloader::before {
  content: "";
  display: block;
  position: absolute;
  border: 3px solid transparent;
  border-radius: 50%;
}
.preloader::before {
  -webkit-animation: r2 1s linear infinite;
  animation: r2 1s linear infinite;
  top: 20px;
  bottom: 20px;
  left: 20px;
  right: 20px;
  border-top-color: inherit;
  -webkit-filter: brightness(150%);
  filter: brightness(150%);
}
.preloader::after {
  -webkit-animation: r2 2s linear infinite;
  animation: r2 2s linear infinite;
  top: 43px;
  bottom: 43px;
  left: 43px;
  right: 43px;
  -webkit-filter: brightness(180%);
  filter: brightness(180%);
  border-top-color: inherit;
}
@-webkit-keyframes r1 {
  from {
    -webkit-transform: rotate(0deg) scale(0.4, 0.4);
    transform: rotate(0deg) scale(0.4, 0.4);
  }
  to {
    -webkit-transform: rotate(360deg) scale(0.4, 0.4);
    transform: rotate(360deg) scale(0.4, 0.4);
  }
}
@keyframes r1 {
  from {
    -webkit-transform: rotate(0deg) scale(0.4, 0.4);
    transform: rotate(0deg) scale(0.4, 0.4);
  }
  to {
    -webkit-transform: rotate(360deg) scale(0.4, 0.4);
    transform: rotate(360deg) scale(0.4, 0.4);
  }
}
@-webkit-keyframes r2 {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@keyframes r2 {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

